<template>
  <v-card class="mx-auto">
    <v-card-title>
      <v-row class="justify-space-between">
        <v-col cols="12" sm="4">
          Заявки
        </v-col>
        <v-col cols="12" sm="4">
          <v-select
            label="Категория" 
            :items="groups"
            v-model="group_type"
          ></v-select>
        </v-col>
        <v-col cols="12" sm="4">
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Поиск"
          ></v-text-field>
        </v-col>
      </v-row>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="filteredItems"
      :search="search"
    >
      <template v-slot:item.userId="props">
        <v-edit-dialog
          :return-value.sync="props.item.userId"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.userId }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.userId"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.date="props">
        <v-edit-dialog
          :return-value.sync="props.item.date"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.date }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.date"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.player="props">
        <v-edit-dialog
          :return-value.sync="props.item.player"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <v-avatar 
            size="35" 
            class="mr-2"
          >
            <img :src="props.item.src">
          </v-avatar>
          <span>{{ props.item.player }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.player"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
            <v-text-field
              v-model="props.item.src"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.operator="props">
        <v-edit-dialog
          :return-value.sync="props.item.operator"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
