        </v-edit-dialog>
      </template>
      <template v-slot:item.withdrawal="props">
        <v-edit-dialog
          :return-value.sync="props.item.withdrawal"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.withdrawal }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.withdrawal"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.status="props">
        <v-edit-dialog
          :return-value.sync="props.item.status"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.status }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.status"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.action>
        <v-btn icon>
          <v-icon small>
            mdi-check-circle-outline
          </v-icon>
        </v-btn>
        <v-btn icon>
          <v-icon small>
            mdi-close-circle-outline
          </v-icon>
        </v-btn>
      </template>
    </v-data-table>

    <v-snackbar 
      v-model="snack" 
      :timeout="3000" 
      :color="snackColor"
    >
      {{ snackText }}
      <v-btn 
        text 
        @click="snack = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </v-card>
</template>

