          player: 'Ваня',
          operator: 'MBANK',
          paymentSystem: 'z.ru',
          wallet: 'qiwi',
          amount: 1000,
          withdrawal: 100,
          status: 'process',
          src: 'https://cdn.vuetifyjs.com/images/john.jpg'
        },
        { 
