          date: new Date().toISOString().substr(0, 10),
          player: 'Катя',
          operator: 'MBANK',
          paymentSystem: 'x.ru',
          wallet: 'webmoney',
          amount: 200,
          withdrawal: 500,
          status: 'cancel',
          src: 'https://cdn.vuetifyjs.com/images/john.jpg'
        },
