          userId: 5,
          date: new Date().toISOString().substr(0, 10),
          player: 'Артем',
          operator: 'MBANK',
          paymentSystem: 'y.ru',
          wallet: 'qiwi',
          amount: 600,
          withdrawal: 100,
          status: 'process',
          src: 'https://cdn.vuetifyjs.com/images/john.jpg'
