          src: 'https://cdn.vuetifyjs.com/images/john.jpg'
        }
      ]
    }),
    methods: {
      save () {
        this.snack = true;
        this.snackColor = 'success';
        this.snackText = 'Данные обновлены';
      },
      cancel () {
        this.snack = true;
        this.snackColor = 'error';
        this.snackText = 'Отмена';
      },
      open () {
        this.snack = true;
        this.snackColor = 'info';
        this.snackText = 'Редактирование';
      },
      close () {
        console.log('Dialog closed');
      }
    },
    computed: {
      filteredItems() {
        return this.data.filter((item) => {
          return (this.group_type == 'All') ? item : !this.group_type || (item.status.toLowerCase() === this.group_type.toLowerCase());
        });
      }
    }
  }
</script>
